<template>
    <div class="collection">
        <van-nav-bar
        title="我的收藏"
        left-text="返回"
        @click-left="back"
        left-arrow
        />
         <!-- 遍历商品数据 -->
            <div class="box_content_list">
                <ul class="clearfix">
                    <li v-for="(item, index) in userCollectionData" :key="index" @click=" goDetail(item.pid)">
                        <div class="listImg">
                             <img class="auto-img"  v-lazy="item.smallImg" alt="">
                        </div>
                        <div class="list_content">
                            <div class="pro-name on-title">{{item.name}}</div>
                            <div class="pro-enName on-title">{{item.enname}}</div>
                            <div class="pro-price on-title">
                                <span>￥{{item.price}}</span>
                                <div class="pro_islike" @click.stop="cancelCollection(item,index)">
                                     <img class="auto-img" :src="require('../assets/images/like_active.png')"  alt=""/>
                                </div>
                            </div>
                        </div>
                    </li>
                     
                </ul>
            </div>
    </div>
</template>

<script>
import '../assets/css/collection.less'
    export default {
        data(){
            return {
                userCollectionData:[],
                isLike: 'like'
            }
        },
        created(){
            this.getAllCollection()
        },
        methods:{
            getAllCollection(){
                let tokenString = localStorage.getItem('__Tk');

                this.$toast.loading({
                    message: '加载中...',
                    forbidClick: true,
                    duration: 0
                })

                this.axios({
                    method: "GET",
                    url: '/findAllLike',
                    params:{
                       appkey: this.appkey,
                       tokenString,
                    }
                }).then(result=>{
                    this.$toast.clear();
                    if(result.data.code==2000){
                        this.userCollectionData=result.data.result;
                    }
                   
                    
                }).catch(err=>{
                    this.$toast.clear();
                    
                })
            },
            cancelCollection(item,index){
                 let tokenString = localStorage.getItem('__Tk');

                this.$toast.loading({
                    message: '加载中...',
                    forbidClick: true,
                    duration: 0
                })

                this.axios({
                    method: "POST",
                    url: '/notlike',
                    data:{
                        appkey: this.appkey,
                        pid: item.pid,
                        tokenString
                    }
                }).then(result=>{
                    this.$toast.clear();
                    this.userCollectionData.splice(index,1);

                    
                }).catch(err=>{
                    this.$toast.clear();
                    
                })
            },
            back(){
                this.$router.go(-1);
            },
             goDetail(pid){
                 //pid: 商品id
                  this.$router.push({name: 'Detail', query: {pid}});
            }
        }
    }
</script>
